// import file
@import "settings";

.btn{
	position: relative;
	z-index: 5;
	top: 17.5rem;
	@include btn;
}

	/* Centered texts in each section
	* --------------------------------------- */
	#bg_video{
		position: absolute;
		z-index: 4;
		right: 0;
		bottom: 0;
		top:0;
		right:0;
		width: 100%;
		height: 100%;
		background-size: 100% 100%;
		background-color: black; /* in case the video doesn't fit the whole page*/
		background-image: url(img/header_video.mp4);
		background-position: center center;
		background-size: contain;
		object-fit: cover; /*cover video background */
	}

		/* Layer with position absolute in order to have it over the video
		* --------------------------------------- */
		#header .slogan{
			position: absolute;
			z-index: 5;
			width: 100%;
			left: 0;
			top: 17.5rem;
			h1{
				@include header_heading;
			}
		}

		.fa-play-circle,
		.fa-pause-circle{
			font-size: 6em;
		}

		.play_btn{
			display: block;
			position: relative;
			z-index: 5;
			top: 11rem;
			margin: 0 auto;
		}
		.smoll-text.text-center{
				z-index: 5;
				@include Small;
				top: 195px;
				position: relative;
				top: 195px;
			} 
